export class ColumnsForm extends Form {
    constructor() {
        super('columns', ColumnsBlock);
        this.numCol = 2;
    }

    render(el) {
        super.render(el);
        this.$addedInputs = document.querySelector('#addedInputs');
        this.$addInputBtn = document.querySelector('#addInputBtn')
        this.$addInputBtn.addEventListener('click', this.addInput.bind(this))
    }

    addInput(e) {
        e.preventDefault();
        this.numCol++;
        this.$addedInputs.insertAdjacentHTML('beforeend',
            input(`value${this.numCol}`, `–¢–µ–∫—Å—Ç ${this.numCol}`)
        );
    }

    toHTML() {

        return form(this.type, '–ö–æ–ª–æ–Ω–∫–∏', [
            input('value1','–¢–µ–∫—Å—Ç 1'),
            input('value2','–¢–µ–∫—Å—Ç 2'),
            `<div id ='addedInputs'></div>`,
            `<button id="addInputBtn" class="btn btn-success">–î–æ–±–∞–≤–∏—Ç—å –µ—â–µ –æ–¥–Ω—É –∫–æ–ª–æ–Ω–∫—É</button>`,
            input('style', '–°—Ç–∏–ª–∏ –¥–ª—è –±–ª–æ–∫–∞' ),
            addButton()
        ])
    }
}

